export default {
  computed: {
    choosedServiceTypesState() {
      const choosedServiceTypes = this.$store.getters['ChoosedServiceTypesStore/choosedSeviceTypesState']
      if (choosedServiceTypes) {
        return choosedServiceTypes
      } else return []
    }
  },
  methods: {
    setChoosedServiceTypesState(serviceTypes) {
      this.$store.dispatch('ChoosedServiceTypesStore/setChoosedSeviceTypes', serviceTypes)
    }
  }
}